.option_item {
  cursor: pointer;
}

.option_item:hover {
  color: #b1a879;
}

.option_list {
  background-color: #424242;
  display: inline-block;
  padding: 1rem;
  color: #fff;
  border: 5px solid #b1a879;
  list-style-position: inside;
}

.ingredients {
  display: flex;
  margin-top: 1rem;
}

.ingredients__list_name {
  padding-left: 1rem;
  list-style: none;
}

.ingredients__list_qty {
  list-style: none;
}

.ingredients__item {
  margin-top: 0.2rem;
}
